import React, { Component } from 'react';
import { connect } from 'react-redux'
import styled, { keyframes } from 'styled-components';
import Typing from 'react-typing-animation';
import Delay from 'react-delay';

import { SCREEN_ORIENTATION_PORTRAIT } from 'constants/screen';
import { pageOpen } from 'actions';
import bgImage from 'images/bg-lg.png';

const ShowUpAnimation = keyframes`
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
`;

const ButtonAnimation = keyframes`
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
`;

const Overlay = styled.div`
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 106;
`;

const sidebarSide = '4vw';
const Container = styled.div`
  overflow: hidden;
  position: absolute;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  left: ${({ orientation }) => (orientation === SCREEN_ORIENTATION_PORTRAIT ? '0' : sidebarSide)};
  bottom: ${({ orientation }) => (orientation === SCREEN_ORIENTATION_PORTRAIT ? sidebarSide : '0')};
  top: 0;
  right: 0;
  background-color: black;
  background-image: ${({ image }) => (`url(${image})` || 'none')};
  background-repeat: no-repeat;
  background-size: cover;
`;

const LabelsContainer = styled.div`
  margin-right: 20%;
  text-align: right;
`;

const LabelTitleFirst = styled.div`
  font-family: Ropa Sans;
  font-style: normal;
  font-weight: light;
  line-height: normal;
  font-size: 3vw;
  text-align: right;
  color: #FFFFFF;
  transform: translateY(50px);
  opacity: 0;
  animation-duration: 0.5s;
  animation-timing-function: ease-out;
  animation-delay: 1s;
  animation-iteration-count: 1;
  animation-name: ${ShowUpAnimation};
  animation-fill-mode: forwards;
`;

const LabelTitleSecond = LabelTitleFirst.extend`
  margin-top: 4vw;
  animation-delay: 2s;
`;

const ButtonContainer = styled.div`
  transform: translateY(50px);
  opacity: 0;
  animation-duration: 0.5s;
  animation-timing-function: ease-out;
  animation-delay: 4.2s;
  animation-iteration-count: 1;
  animation-name: ${ShowUpAnimation};
  animation-fill-mode: forwards;
`;

const Button = styled.button`
  background: #0C0C0C;
  border-radius: 100px;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0.5vw 1.7vw;
  font-family: Ropa Sans;
  font-style: normal;
  font-weight: light;
  line-height: normal;
  font-size: 2.5vw;
  text-align: center;
  color: #FFFFFF;
  margin-top: 5vw;
  &:focus {
    animation: ${ButtonAnimation} 0.3s;
  }
`;

{/* <Typing startDelay={1000} {...typingProps}>
            <BigLabel>Hello! I'm Airomad</BigLabel>
          </Typing> */}

class MainPageContainer extends Component {
  render() {
    const { onPageOpen, orientation } = this.props;
    return (
      <Container orientation={orientation} image={bgImage}>
        <LabelsContainer>
          <LabelTitleFirst>Hello, I’m Ivan Stinsky</LabelTitleFirst>
          <LabelTitleSecond>I’m a frontend developer<br />from Ukraine</LabelTitleSecond>
          <ButtonContainer>
            <Button onClick={() => onPageOpen('MESSAGE')}>Get in touch</Button>
          </ButtonContainer>
        </LabelsContainer>
      </Container>
    );
  }
}

function mapStateToProps(state) {
  return {
    page: state.app.page
  };
}

function mapDispatchToProps(dispatch, ownProps) {
  return {
    onPageOpen: (page) => dispatch(pageOpen(page))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(MainPageContainer);



