import React, { Component } from 'react';
import { connect } from 'react-redux'
import styled from 'styled-components';

import { screenOrientationChange, pageOpen } from 'actions';
import { SCREEN_ORIENTATION_LANDSCAPE, SCREEN_ORIENTATION_PORTRAIT } from 'constants/screen';

import NavBar from 'components/NavBar';
import MainPageContainer from 'containers/pages/MainPageContainer';
import FeedPageContainer from 'containers/pages/FeedPageContainer';
import WorksPageContainer from 'containers/pages/WorksPageContainer';
import MessagePageContainer from 'containers/pages/MessagePageContainer';
import GithubPageContainer from 'containers/pages/GithubPageContainer';
import SkillsPageContainer from 'containers/pages/SkillsPageContainer';
import PageContainer from 'containers/PageContainer';

const Container = styled.div`
  height: 100vh;
  width: 100%;
  margin: -1px;
  padding: 1px;
`;

const TempPage = styled.div`
  width: 100%;
  height: 100vh;
  background: red;
`;

const TempPage2 = styled.div`
  width: 100%;
  height: 100vh;
  background: blue;
`;

class AppContainer extends Component {
  componentDidMount() {
    window.addEventListener('resize', this.resizeWindowHandle);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.resizeWindowHandle);
  }

  resizeWindowHandle = () => {
    const { width, height } = screen;
    console.log(width, height);
    if (width > height) {
      if (this.props.orientation !== SCREEN_ORIENTATION_LANDSCAPE) {
        this.props.onScreenOrientationChange(SCREEN_ORIENTATION_LANDSCAPE);
      }
    } else {
      if (this.props.orientation !== SCREEN_ORIENTATION_PORTRAIT) {
        this.props.onScreenOrientationChange(SCREEN_ORIENTATION_PORTRAIT);
      }
    }
  }

  // renderModal = () => {
  //   const { page } = this.props;
  //   switch (page) {
  //     case 'MESSAGE': return <MessagePageContainer visible={page === 'ME/>
  //     default: return null;
  //   }
  //

  renderModalPage = (Component, title, isVisible) => {
    const { orientation, onPageClose } = this.props;
    return (
      <PageContainer visible={isVisible} title={title} onClose={onPageClose} orientation={orientation}>
        <Component />
      </PageContainer>
    );
  }

  render() {
    const { orientation, page } = this.props;
    const pageProps = {
      orientation
    };
    return (
      <Container>
        <NavBar orientation={this.props.orientation} />
        <MainPageContainer {...pageProps} />
        { this.renderModalPage(FeedPageContainer, 'News Feed', page === 'FEED') }
        { this.renderModalPage(WorksPageContainer, 'Last Works', page === 'WORKS') }
        { this.renderModalPage(MessagePageContainer, 'Get In Touch With Me', page === 'MESSAGE') }
        { this.renderModalPage(GithubPageContainer, 'My Github Projects', page === 'GITHUB') }
        { this.renderModalPage(SkillsPageContainer, 'My Skills', page === 'SKILLS') }
      </Container>
    );
  }
}

function mapStateToProps(state) {
  return {
    orientation: state.screen.orientation,
    page: state.app.page,
    URL: state.routing.locationBeforeTransitions
        ? state.routing.locationBeforeTransitions.hash.substring(1)
        : (window.location.hash && window.location.hash !== '#')
            ? window.location.hash.substr(1)
            : '/'
  };
}

function mapDispatchToProps(dispatch, ownProps) {
  return {
    onScreenOrientationChange: (orientation) => dispatch(screenOrientationChange(orientation)),
    onPageClose: () => dispatch(pageOpen('HOME'))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(AppContainer);



