import { ON_SCREEN_ORIENTATION_CHANGE } from 'constants/screen';
import { PAGE_OPEN } from 'constants/app';

export function screenOrientationChange(orientation) {
  return {
    type: ON_SCREEN_ORIENTATION_CHANGE,
    orientation
  }
}

export function pageOpen(page) {
  return {
    type: PAGE_OPEN,
    page
  }
}



