import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';

import { SCREEN_ORIENTATION_PORTRAIT } from 'constants/screen';
import { Container, LabelTitle } from './commonStyles';

const Wrapper = Container.extend`
  justify-content: center;
  align-items: center;
`;

const Row = styled.div`
  display: flex;
  flex-direction: row;
  padding-right: 50px;
  padding-left: 50px;
`;

const LeftCell = styled.div`
  flex: 1;
  font-family: Ropa Sans;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 1.9vw;
  text-align: right;
  color: #000000;
  margin-top: 20px;
  margin-bottom: 20px;
`;

const RightCell = LeftCell.extend`
  color: #A6A6A6;
  text-align: left;
`;

const Separator = styled.div`
  width: 1px;
  height: auto;
  background: #9C9C9C;
  margin-left: 60px;
  margin-right: 60px;
`;

export default class SkillsPageContainer extends Component {
  render() {
    return (
      <Wrapper>
        <Row>
          <LeftCell>Languages</LeftCell>
          <Separator />
          <RightCell>Javascript (ES6), TypeScript, CSS3, HTML5, PHP, SQL</RightCell>
        </Row>
        <Row>
          <LeftCell>Libraries / Frameworks</LeftCell>
          <Separator />
          <RightCell>jQuery, KnockoutJS, ReactJS, React Native</RightCell>
        </Row>
        <Row>
          <LeftCell>Tools</LeftCell>
          <Separator />
          <RightCell>Webpack2, NPM, Gulp, Figma, Zeplin, Photoshop, Adobe Illustrator</RightCell>
        </Row>
      </Wrapper>
    );
  }
}



