import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';

import { SCREEN_ORIENTATION_PORTRAIT } from 'constants/screen';
import { Container, LabelTitle } from './commonStyles';

import octocatImg from 'images/octocat-lg.png';

const ButtonAnimation = keyframes`
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
`;

const Wrapper = Container.extend`
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const Button = styled.button`
  background: #0C0C0C;
  border-radius: 100px;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0.5vw 1.7vw;
  font-family: Ropa Sans;
  font-style: normal;
  font-weight: light;
  line-height: normal;
  font-size: 2.5vw;
  text-align: center;
  color: #FFFFFF;
  margin-top: 5vw;
  &:focus {
    animation: ${ButtonAnimation} 0.3s;
  }
`;

export default class WorksPageContainer extends Component {
  render() {
    return (
      <Wrapper>
        <img src={octocatImg} width={200} height={150} />
        <Button onClick={() => document.location.href="https://github.com/airomad"}>Open Github</Button>
      </Wrapper>
    );
  }
}



