import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';
import ReactLoading from 'react-loading';

import { SCREEN_ORIENTATION_PORTRAIT } from 'constants/screen';
import statusErrorImg from 'images/status-error-icon.png';
import statusOkImg from 'images/status-ok-icon.png';
import { Container } from './commonStyles';

const ButtonAnimation = keyframes`
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
`;

const Wrapper = Container.extend`
  flex-direction: row;
  padding-bottom: 50px;
  align-items: stretch;
  
  @media (max-width: 1100px) {
    flex-direction: column;
  }
`;

const LeftCell = styled.div`
  height: auto;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  padding-left: 50px;
  @media (max-width: 1100px) {
    padding-right: 50px;
  }
`;

const LabelLink = styled.div`
  font-family: Ropa Sans;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 30px;
  text-align: right;
  color: #000000;
  margin-top: 20px;
  margin-bottom: 20px;
  & a {
    color: black;
    text-decoration: none;
  }
  @media (max-width: 768px) {
    font-size: 20px;
    margin-bottom: 16px;
  }
  @media (max-width: 500px) {
    font-size: 16px;
    margin-bottom: 12px;
  }
`;

const SpanGrey = LabelLink.extend`
  color: #A6A6A6;
  display: inline-block;
  margin: 0px;
  margin-right: 5px;
`;

const Separator = styled.div`
  width: 1px;
  height: 100%;
  background: #9C9C9C;
  margin-left: 60px;
  margin-right: 60px;
  @media (max-width: 1100px) {
    display: none;
  }
`;

const RightCell = LeftCell.extend`
  justify-content: center;
  align-items: stretch;
  padding-left: 0px;
  padding-right: 50px;
  @media (max-width: 1100px) {
    padding-left: 50px;
  }
`;

const MessageFormContainer = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
`;

const SpinnerFormContainer = MessageFormContainer.extend`
  justify-content: center;
  align-items: center;
  @media (max-width: 1100px) {
    justify-content: flex-start;
  }
`;

const LabelTitle = styled.div`
  font-family: Ropa Sans;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 30px;
  text-align: center;
  color: #000000;
  margin-top: 20px;
  margin-bottom: 45px;
  @media (max-width: 1100px) {
    margin-top: 100px;
  }
`;

const InputStyles = `
  font-family: Ropa Sans;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 30px;
  color: black;
  background: #F8F8F8;
  border: 1px solid #D4D4D4;
  padding: 18px 20px;
  margin-bottom: 25px;
  &::placeholder {
    color: #A6A6A6;
  }
`;

const InputField = styled.input`
  ${InputStyles}
`;

const TextareaField = styled.textarea`
  min-height: 234px;
  ${InputStyles}
`;

const ButtonContainer = styled.div`
  display: flex;
  justify-content: flex-end;
  min-height: 66px;
  margin-bottom: 40px;
`;

const Button = styled.button`
  background: #A00BA3;
  border-radius: 100px;
  font-family: Ropa Sans;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 30px;
  text-align: center;
  color: #FFFFFF;
  cursor: pointer;
  border: none;
  outline: none;
  padding: 17px 31px;
  &:focus {
    animation: ${ButtonAnimation} 0.3s;
  }
`;

const ImageContainer = styled.div`
  width: 160px;
  height: 160px;
  border: 1px solid red;
`;

const MESSAGE_FORM_STATE_INIT = 'MESSAGE_FORM_STATE_INIT';
const MESSAGE_FORM_STATE_SENDING = 'MESSAGE_FORM_STATE_SENDING';
const MESSAGE_FORM_STATE_ERROR = 'MESSAGE_FORM_STATE_ERROR';
const MESSAGE_FORM_STATE_OK = 'MESSAGE_FORM_STATE_OK';

export default class WorksPageContainer extends Component {
  state = {
    formState: MESSAGE_FORM_STATE_OK
  };

  renderMessageForm = () => {
    switch (this.state.formState) {
        case MESSAGE_FORM_STATE_SENDING:
          return (
            <SpinnerFormContainer>
              <ReactLoading
                type="cylon"
                color="#000000"
                height={40}
                width={150}
              />
              <LabelTitle style={{ marginTop: 100}}>Sending your message..</LabelTitle>
            </SpinnerFormContainer>
          );

        case MESSAGE_FORM_STATE_ERROR:
        return (
          <SpinnerFormContainer>
            <img src={statusErrorImg} alt="Error Icon" />
            <LabelTitle style={{ marginTop: 50}}>Error! Sorry, there was an error while sending you message :(</LabelTitle>
            <ButtonContainer>
              <Button onClick={this.clearMessageForm}>Try again</Button>
            </ButtonContainer>
          </SpinnerFormContainer>
        );

        case MESSAGE_FORM_STATE_OK:
        return (
          <SpinnerFormContainer>
            <img src={statusOkImg} alt="Ok Icon" />
            <LabelTitle style={{ marginTop: 50}}>Yes! Your message was sent successfully!</LabelTitle>
            <ButtonContainer>
              <Button onClick={this.clearMessageForm}>Send new one</Button>
            </ButtonContainer>
          </SpinnerFormContainer>
        );

        case MESSAGE_FORM_STATE_INIT: default :
        return (
          <MessageFormContainer>
            <InputField
              placeholder="Your name"
            />
            <InputField
              placeholder="Subject"
            />
            <TextareaField
              placeholder="Message"
            />
            <ButtonContainer>
              <Button onClick={this.submitMessageForm}>Send message</Button>
            </ButtonContainer>
          </MessageFormContainer>
        );
    }
  }

  submitMessageForm = () => {
    this.setState({ formState: MESSAGE_FORM_STATE_SENDING });
    setTimeout(() => {
      this.setState({ formState: MESSAGE_FORM_STATE_ERROR })
    }, 500);
  }

  clearMessageForm = () => this.setState({ formState: MESSAGE_FORM_STATE_INIT });

  render() {
    return (
      <Wrapper>
        <LeftCell>
          <LabelLink>
            <SpanGrey>Email:</SpanGrey>
            <a href="mailto:airomad7@gmail.com" target="_blank">airomad7@gmail.com</a>
          </LabelLink>
          <LabelLink>
            <SpanGrey>Telegram:</SpanGrey>
            <a href="https://t.me/airomad" target="_blank">@airomad</a>
          </LabelLink>
          <LabelLink>
            <SpanGrey>LinkedIn:</SpanGrey>
            <a href="https://www.linkedin.com/in/airomad" target="_blank">www.linkedin.com/in/airomad</a>
          </LabelLink>
        </LeftCell>
        <Separator />
        <RightCell>
          <LabelTitle>Here will be a form to send messages directly to Telegram</LabelTitle>
          {/* { this.renderMessageForm() } */}
        </RightCell>
      </Wrapper>
    );
  }
}



