import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';
import Loader from 'react-loader-spinner';

import { SCREEN_ORIENTATION_PORTRAIT } from 'constants/screen';
import { Container, LabelTitle } from './commonStyles';

const PostContainer = styled.div`
  margin: 10px;
`;

const LoadingOverlayContainer = styled.div`
  position: absolute;
  left: 0;
  top: 80px;
  right: 0;
  bottom: 0;
  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export default class FeedPageContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      postsId: [
        8, 9
      ],
      isLoading: true
    }
  }

  componentDidMount() {
    setTimeout(() => this.setState({ isLoading: false }), 900);
    this.renderPosts();
  }

  renderPostContainers = () => {
    return this.state.postsId.map(postId => <PostContainer innerRef={el => (this[`postRef${postId}`] = el)} />);
  }

  renderPosts = () => {
    setTimeout(() => {
      this.state.postsId.forEach((postId) => {
        const script = document.createElement('script');
        script.src = 'https://telegram.org/js/telegram-widget.js?1';
        script.setAttribute('data-telegram-post', `ivanstinsky_pro/${postId}`);
        script.setAttribute('data-width', '100%');
        script.async = true;
        this[`postRef${postId}`].appendChild(script);
      });
    }, 200);
  }

  render() {
    return (
      <Container>
        { this.renderPostContainers() }

        { this.state.isLoading &&
          <LoadingOverlayContainer>
            <Loader
              type="Bars"
              color="#4d4d4d"
              height={40}
              width={40}
            />
          </LoadingOverlayContainer>
        }
      </Container>
    );
  }
}



