import { PAGE_OPEN } from 'constants/app'

const INITIAL_STATE = {
  page: 'HOME'
}

export default function app(state = INITIAL_STATE, action) {
  switch (action.type) {
    case PAGE_OPEN:
      return {
        ...state,
        page: action.page
      };

    default:
      return state;
  }
}



