import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';

import { SCREEN_ORIENTATION_PORTRAIT } from 'constants/screen';
import CloseIcon from 'icons/CloseIcon';
import { HeaderContainer, LabelTitle, CloseButton } from './pages/commonStyles';

const ShowUpOverlayAnimation = keyframes`
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
`;

const ShowUpContainerAnimation = keyframes`
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
`;

const HideInOverlayAnimation = keyframes`
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
`;

const HideInContainerAnimation = keyframes`
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
`;

const Wrapper = styled.div`
  overflow: hidden;
  position: absolute;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  left: ${({ orientation }) => (orientation === SCREEN_ORIENTATION_PORTRAIT ? '0' : '50px')};
  bottom: ${({ orientation }) => (orientation === SCREEN_ORIENTATION_PORTRAIT ? '50px' : '0')};
  top: 0;
  right: 0;
  background-color: rgba(0,0,0,0.5);
  animation-duration: 0.5s;
  animation-timing-function: ease-out;
  animation-delay: 0s;
  animation-iteration-count: 1;
  animation-name: ${({ hideIn }) => (hideIn ? HideInOverlayAnimation : ShowUpOverlayAnimation)};
  animation-fill-mode: forwards;
`;

const Container = styled.div`
  position: absolute;
  left: 25px;
  right: 25px;
  top: 25px;
  bottom: 25px;
  background: white;
  animation-duration: 0.3s;
  animation-timing-function: ease-out;
  animation-delay: 0s;
  animation-iteration-count: 1;
  animation-name: ${({ hideIn }) => (hideIn ? HideInContainerAnimation : ShowUpContainerAnimation)};
  animation-fill-mode: forwards;
  overflow-x: hidden;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  @media (max-width: 768px) {
    left: 25px;
    top: 25px;
    right: 25px;
    bottom: 25px;
  }
  @media (max-width: 500px) {
    left: 15px;
    top: 15px;
    right: 15px;
    bottom: 15px;
  }
`;

export default class PageContainer extends Component {
  state = {
    isRender: this.props.visible
  };

  componentWillReceiveProps(nextProps) {
    if (this.props.visible && !nextProps.visible) {
      setTimeout(() => this.setState({ isRender: false }), 300)
    } else if (!this.props.visible && nextProps.visible) {
      this.setState({ isRender: true })
    }
  }

  render() {
    const { visible, orientation, children, title, onClose } = this.props;
    return this.state.isRender
      ? (
        <Wrapper orientation={orientation} hideIn={!visible}>
          <Container hideIn={!visible}>
            <HeaderContainer>
              <CloseButton onClick={onClose}>
                <CloseIcon />
              </CloseButton>
              <LabelTitle>{title}</LabelTitle>
            </HeaderContainer>
            {children}
          </Container>
        </Wrapper>
      )
      : null;
  }
}



