import React, { Component } from 'react';
import { connect } from 'react-redux';
import styled, { keyframes } from 'styled-components';

import { SCREEN_ORIENTATION_PORTRAIT } from 'constants/screen';
import { pageOpen } from 'actions';
import FeedIcon from 'icons/FeedIcon';
import WorksIcon from 'icons/WorksIcon';
import MessageIcon from 'icons/MessageIcon';
import GithubIcon from 'icons/GithubIcon';
import SkillsIcon from 'icons/SkillsIcon';

const ButtonAnimation = keyframes`
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
`;

const sidebarSide = '4vw';
const Container = styled.div`
  position: absolute;
  width: ${({ orientation }) => (orientation === SCREEN_ORIENTATION_PORTRAIT ? '100%' : sidebarSide)};
  height: ${({ orientation }) => (orientation === SCREEN_ORIENTATION_PORTRAIT ? sidebarSide : '100%')};
  top: ${({ orientation }) => (orientation === SCREEN_ORIENTATION_PORTRAIT ? 'auto' : '0px')};
  bottom: ${({ orientation }) => (orientation === SCREEN_ORIENTATION_PORTRAIT ? '0px' : 'auto')};
  left: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #0C0C0C;
  color: red;
  z-index: 200;
`;

const ButtonsContainer = styled.div`
  display: flex;
  flex: 1;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: ${({ orientation }) => (orientation === SCREEN_ORIENTATION_PORTRAIT ? 'row' : 'column')};
  overflow: hidden;
`;

const Button = styled.button`
  background: transparent;
  border: none;
  width: 3.5vw;
  height: 3.5vw;
  position: relative;
  display: flex;
  outline: none;
  cursor: pointer;
  margin: 5px;
  &:focus {
    animation: ${ButtonAnimation} 0.3s;
  }
`;

class NavBar extends Component {
  renderButton = (IconComponent, currentPage) => {
    const { onPageOpen, page } = this.props;
    return (
      <Button onClick={() => onPageOpen(currentPage)}>
        <IconComponent active={page === currentPage}/>
      </Button>
    );
  }

  render() {
    const { orientation } = this.props;
    return (
      <Container orientation={orientation}>
        <ButtonsContainer orientation={orientation}>
          { this.renderButton(FeedIcon, 'FEED') }
          { this.renderButton(SkillsIcon, 'SKILLS') }
          { this.renderButton(WorksIcon, 'WORKS') }
          { this.renderButton(MessageIcon, 'MESSAGE') }
          { this.renderButton(GithubIcon, 'GITHUB') }
        </ButtonsContainer>
      </Container>
    );
  }
}

function mapStateToProps(state) {
  return {
    page: state.app.page
  };
}

function mapDispatchToProps(dispatch, ownProps) {
  return {
    onPageOpen: (page) => dispatch(pageOpen(page))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(NavBar);



