import styled from 'styled-components';

export const Container = styled.div`
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  flex: 1;
  justify-content: flex-start;
  align-items: stretch;
`;

export const HeaderContainer = styled.div`
  display: flex;
  flex-direction: row;
  @media (max-width: 768px) {
    margin-bottom: 40px;
  }
`;

export const CloseButton = styled.button`
  border: none;
  outline: none;
  cursor: pointer;
  background: transparent;
  margin-top: 20px;
  margin-left: 50px;
  margin-right: 20px;
`;

export const LabelTitle = styled.div`
  flex: 1;
  font-family: Ropa Sans;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 30px;
  text-align: right;
  color: #000000;
  margin-top: 30px;
  padding-right: 90px;
  @media (max-width: 1100px) {
    padding-right: 50px;
  }
  @media (max-width: 768px) {
    font-size: 40px;
  }
  @media (max-width: 500px) {
    font-size: 30px;
  }
`;



